.menu {
    display: flex;
    justify-content: end;
    margin: 0;
    width: 100%;
    padding: 0;
    right: 0;
    background: linear-gradient(to right, #3E007C, #000023);
  }
  
  .menu ul li a{
    text-decoration: none;
    color: white;
  }

  .menu ul li{
    list-style: none;
    height: fit-content;
  }

  .menu ul{
    display: flex;
    justify-content: end;
    gap: 100px;
    width: fit-content;
    height: fit-content;
    padding-right: 2rem;
    margin: 1.5rem;
  }

.logo{
    position: absolute;
    transform: translateY(-5vh);
  }

  .logo p{
    text-decoration: none;
    color: white;
  }

  @media only screen and (max-width: 900px) {
    #logo {
      width: 10vw;
    }

    .menu {
      margin: 0;
      width: 100%;
      right: 0;
    }

    .menu ul{
      display: flex;
      justify-content: end;
      gap: 20px;
      width: fit-content;
      height: fit-content;
      padding-right: 2rem;
      margin: 1.5rem;
    }
}